import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import {HttpClientModule} from '@angular/common/http'
import { Routes, RouterModule } from '@angular/router';
import { LoginComponent } from './login/login.component';
import { AdminComponent } from './admin.component';
import { HomeComponent } from './home/home.component';
import { ListComponent } from './home/movies/list/list.component';
import { ReUsableModule } from '../re-usable/admin/re-usable.module';
import { AddEditComponent } from './home/movies/add-edit/add-edit.component';
import { NgMultiSelectDropDownModule } from 'ng-multiselect-dropdown-angular7';
import {FormsModule} from '@angular/forms';
import { AuthGuard } from '../providers/auth.guard';
import { FlashMessagesModule } from 'angular2-flash-messages';
const routes: Routes = [{
  path: '',component:AdminComponent, children: [
    { path: 'login',component:LoginComponent,pathMatch:'full' },
    { path: '', component:HomeComponent,canActivate:[AuthGuard], children:[
      { path:'movies', component:ListComponent},
       {path:'movies/add-edit/:id',component:AddEditComponent},
       {path:'movies/add-edit',component:AddEditComponent}
    ]},
  ]
}];

@NgModule({
  declarations:[LoginComponent,AdminComponent,ListComponent,HomeComponent,AddEditComponent],
  imports: [FlashMessagesModule.forRoot(),CommonModule,RouterModule.forChild(routes),ReUsableModule,NgMultiSelectDropDownModule,HttpClientModule,FormsModule],
  exports: [RouterModule],
})
export class AdminRoutingModule { }
