import { Component, OnInit } from '@angular/core';
import { ApiService } from 'src/app/providers/api.service';
import { Router,ActivatedRoute } from '@angular/router';
import { FlashMessagesService } from 'angular2-flash-messages';
import { MessagesService } from 'src/app/providers/messages.service';
@Component({
  selector: 'app-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.css'],
  providers:[FlashMessagesService]
})
export class ListComponent implements OnInit {
  public collectData=[];
  public filters={getType: ""};
  public loader={deleteMovie:false};
  public loadingMessage={type:0,message:''};

  constructor(public flashMessagesService:FlashMessagesService,private messagesService:MessagesService,private route:ActivatedRoute,private api:ApiService) { }

 

  ngOnInit() {
    let getMessage= this.messagesService.getMessage();
    if(getMessage &&  getMessage['type']=="success"){
     this.flashMessagesService.show(getMessage['text'], { cssClass: 'alert-success', timeout: 3000 });
     this.messagesService.setMessage('','')
    }
    this.allMovies();
  }

  allMovies(){
    this.loadingMessage={type:1,message:'Loading...'};
    this.api.getMovies(this.filters).subscribe((data)=>{
        if(data['type']=='success'){
          this.collectData=data['details']
        }
        this.loadingMessages()
    })
  }
  search(){
    this.loadingMessage={type:1,message:'Loading...'};
    this.api.getMovies(this.filters).subscribe((data)=>{
      if(data['type']=='success'){
        this.collectData=data['details']
      }
      this.loadingMessages();
  })
  }
  reset(){
    this.loadingMessage={type:1,message:'Loading...'};
    this.filters={getType: ""};
    this.api.getMovies(this.filters).subscribe((data)=>{
      if(data['type']=='success'){
        this.collectData=data['details']
      }
      this.loadingMessages();
  })
  }
  deleteMovie(id,index,event){
    event.target.getElementsByClassName('moveLoader')[0].style.display='block';
   this.api.deleteMovie({id:id}).subscribe((data)=>{
    this.loadingMessages();
    event.target.getElementsByClassName('moveLoader')[0].style.display='none';
    if(data['type']=='success'){
     this.flashMessagesService.show('Movie deleted successfully', { cssClass: 'alert-danger', timeout: 3000 });

      this.collectData.splice(index,1);
    }
    },err=>event.target.getElementsByClassName('moveLoader')[0].style.display='none')
  }
  loadingMessages(){
    if(this.collectData.length==0){
      this.loadingMessage={type:2,message:'No Data Found'};
    }else{
      this.loadingMessage={type:0,message:''};
    }
  }
}
