import { Component, OnInit } from '@angular/core';
import { ApiService } from 'src/app/providers/api.service';
import {Router} from '@angular/router';
import { FlashMessagesService } from 'angular2-flash-messages';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css'],
  providers:[FlashMessagesService]
})
export class LoginComponent implements OnInit {
  public userData={email:'',password:''}

  constructor(private flashMessagesService:FlashMessagesService,private api:ApiService,private router:Router) { }

  ngOnInit() {
    let getUserDetails=localStorage.getItem('userData');
    if(getUserDetails){
     this.router.navigate(['/admin/movies']);
    }
   return true;
  }

  onSubmit(event){
    event.target.getElementsByClassName('moveLoader')[0].style.display='block';
    this.api.userLogin(this.userData).subscribe((data)=>{
      event.target.getElementsByClassName('moveLoader')[0].style.display='none';
      if(data && data['type'] && data['type']=='success'){
        localStorage.setItem('userData', JSON.stringify(data));
        this.router.navigate(['admin/movies']);
      }else{
        this.flashMessagesService.show('Invalid credentials', { cssClass: 'alert-danger', timeout: 3000 });
        localStorage.removeItem('userData')
      }
    })
  }

}
