import { Component, OnInit } from '@angular/core';
import { ApiService } from 'src/app/providers/api.service';
import { ActivatedRoute, Router } from '@angular/router';
import { FlashMessagesService } from 'angular2-flash-messages';
import { Observable, Subject } from 'rxjs';
import { MessagesService } from 'src/app/providers/messages.service';
@Component({
  selector: 'app-add-edit',
  templateUrl: './add-edit.component.html',
  styleUrls: ['./add-edit.component.css'],
  providers: [ApiService,FlashMessagesService]
})
export class AddEditComponent implements OnInit {
  public tags = [];
  private passMessage = new Subject<any>();
  getId = null;
  dropdownList = [];
  selectedItems = [];
  dropdownSettings = {};
  public isSubmit = false;
  public formData = { name: null, genre: null, director: null, popularity: null, imdb_score: null };

  constructor(private messagesService:MessagesService,private flashMessage: FlashMessagesService,private router: Router, private route: ActivatedRoute, private api: ApiService) { }

  ngOnInit() {
    this.route.paramMap.subscribe((data) => {
      this.getId = data.get('id');
      if (this.getId) {
        this.api.getMovies({ getType: 'id', searchText: this.getId }).subscribe((data) => {
          if (data['details'] && data['details'].length > 0) {
            this.formData['name'] = data['details'][0]['name'];
            this.formData['genre'] = data['details'][0]['genre'];
            this.formData['director'] = data['details'][0]['director'];
            this.formData['imdb_score'] = data['details'][0]['imdb_score'];
            this.formData['popularity'] = data['details'][0]['popularity'];
            this.udpateGenre(data);
          }
        })
      }
    })
    this.dropdownList = [
      { item_id: "Adventure", item_text: 'Adventure' },
      { item_id: "Family", item_text: 'Family' },
      { item_id: "Fantasy", item_text: 'Fantasy' },
      { item_id: "Musical", item_text: 'Musical' },
    ];

    this.dropdownSettings = {
      singleSelection: false,
      idField: 'item_id',
      textField: 'item_text',
      selectAllText: 'Select All',
      unSelectAllText: 'UnSelect All',
      itemsShowLimit: 3,
      allowSearchFilter: true
    };

  }

  udpateGenre(data) {
    let collectArray = [];
    if (data['details'][0]['genre'] && data['details'][0]['genre'] != "") {
      let genreToArray = data['details'][0]['genre'].split(',');
      if (genreToArray.length > 0) {
        genreToArray.forEach(element => {
          collectArray.push({ item_id: element, item_text: element })
        });
      }
    }
    this.selectedItems = collectArray;
  }
  onItemSelect(item: any) {
  }
  onSelectAll(items: any) {
  }

  genreGetModified(data) {
    let collectGenre = [];
    if (data.length > 0) {
      data.forEach(element => {
        if (element.item_id) {
          collectGenre.push(element.item_id)
        }
      });
    }
    return collectGenre.length>0?collectGenre:data;
  }
  onSubmit(movieForm,event) {
    this.isSubmit = true;
    if (movieForm.invalid) {
      return false;
    }
    /* update genre */
    if (this.selectedItems.length > 0) {
      this.formData.genre = this.genreGetModified(this.selectedItems).join()
    }
    event.target.getElementsByClassName('moveLoader')[0].style.display='block';

    /* to update the data */
    if (this.getId) {
      this.formData['id'] = this.getId;
      this.api.updateMovie(this.formData).subscribe((data) => {
        event.target.getElementsByClassName('moveLoader')[0].style.display='none';
        this.messagesService.setMessage('Movies updated Successfully','success');
        this.router.navigate(['/admin/movies']);
      }, err => console.log(err))
    } else {
      this.api.createMovie(this.formData).subscribe((data) => {
        event.target.getElementsByClassName('moveLoader')[0].style.display='none';
        this.messagesService.setMessage('Movies added Successfully','success');
        this.router.navigate(['/admin/movies']);
      }, err => console.log(err))
    }
  }

}
